.menuItem {
  position: relative;
  display: inline-block;
  padding: 0 50px;
  border-right: 1px solid #ddd;
  line-height: 50px; }
  .menuItem:first-child {
    border-left: 1px solid #ddd; }
  .menuItem:hover {
    background-color: rgba(0, 0, 0, 0.05);
    cursor: pointer; }
  .menuItem a {
    position: relative;
    display: block;
    width: 100%;
    height: 100%; }
